* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content {
  height: 100vh;
}

.resgister-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.mainpage{
  margin-left: 20px;
  margin-right: 20px;;
}



/* transection page */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 0 rgb(149, 69, 69);
}
/* transection page */

/* antd icons */

.switch-icons {
  border: 1px solid rgba(9, 9, 9, 0.677);
  border-radius: 5px;
  padding: 10px 15px;
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: rgb(32, 28, 28);
}
.inactive-icon {
  color: rgb(86, 29, 29);
}
